import React from 'react';
import { getServerSession } from "next-auth";
import { authOptions } from "@/lib/auth";
import Project from "@/models/Project";
import Experience from "@/models/Experience";
import connectToDatabase from "@/lib/db";
import { Card } from "@/components/ui";
import { Layers, Briefcase, Activity } from 'lucide-react';

async function getStats() {
    await connectToDatabase();
    const projectCount = await Project.countDocuments();
    const experienceCount = await Experience.countDocuments();
    return { projectCount, experienceCount };
}

export default async function AdminDashboard() {
    const session = await getServerSession(authOptions);
    const { projectCount, experienceCount } = await getStats();

    return (
        <div className="space-y-12">
            <header className="flex flex-col md:flex-row md:items-end justify-between gap-4">
                <div>
                    <h1 className="text-5xl font-display font-bold text-primary tracking-tight mb-2">Overview</h1>
                    <p className="text-lg text-secondary">Welcome back, <span className="text-primary font-medium">{session?.user?.name || 'Admin'}</span>.</p>
                </div>
                <div className="flex gap-2 text-xs font-mono text-secondary/40">
                    <span>SYSTEM_ACTIVE</span>
                    <span>â€¢</span>
                    <span>{new Date().toLocaleDateString()}</span>
                </div>
            </header>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                <Card className="hover:border-accent/40 transition-all group">
                    <div className="flex items-center gap-4 mb-4">
                        <div className="p-3 bg-accent/10 rounded-xl text-accent group-hover:bg-accent group-hover:text-black transition-all">
                            <Layers size={20} />
                        </div>
                        <h3 className="text-sm font-bold text-secondary uppercase tracking-widest">Projects</h3>
                    </div>
                    <p className="text-5xl font-display font-bold text-primary">{projectCount}</p>
                    <p className="text-xs text-secondary/50 mt-4 underline group-hover:text-accent transition-colors">Manage projects &rarr;</p>
                </Card>

                <Card className="hover:border-accent/40 transition-all group">
                    <div className="flex items-center gap-4 mb-4">
                        <div className="p-3 bg-accent/10 rounded-xl text-accent group-hover:bg-accent group-hover:text-black transition-all">
                            <Briefcase size={20} />
                        </div>
                        <h3 className="text-sm font-bold text-secondary uppercase tracking-widest">Experience</h3>
                    </div>
                    <p className="text-5xl font-display font-bold text-primary">{experienceCount}</p>
                    <p className="text-xs text-secondary/50 mt-4 underline group-hover:text-accent transition-colors">View timeline &rarr;</p>
                </Card>

                <Card className="hover:border-accent/40 transition-all group">
                    <div className="flex items-center gap-4 mb-4">
                        <div className="p-3 bg-accent/10 rounded-xl text-accent group-hover:bg-accent group-hover:text-black transition-all">
                            <Activity size={20} />
                        </div>
                        <h3 className="text-sm font-bold text-secondary uppercase tracking-widest">Status</h3>
                    </div>
                    <div className="flex items-center gap-2 mt-2">
                        <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse shadow-[0_0_10px_2px_rgba(34,197,94,0.3)]"></div>
                        <span className="text-2xl font-display font-bold text-primary">Operational</span>
                    </div>
                    <p className="text-xs text-secondary/50 mt-6">All systems functioning normally.</p>
                </Card>
            </div>
        </div>
    );
}
